@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600;700;800&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Airbnb+Cereal+App:wght@500&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-open-sans: "Open Sans";
  --font-airbnb-cereal-app: "Airbnb Cereal App";

  /* font sizes */
  --font-size-3xs: 10px;
  --font-size-sm: 14px;
  --font-size-xs: 12px;
  --font-size-lg: 18px;
  --font-size-2xs: 11px;
  --font-size-13xl: 32px;
  --font-size-xl: 20px;
  --font-size-mini: 15px;
  --font-size-base: 16px;
  --font-size-5xl: 24px;

  /* Colors */
  --color-solid-white-100: #fff;
  --color-darkslategray-100: #2e3758;
  --color-darkslategray-200: #33344f;
  --color-darkslategray-300: #282e44;
  --color-solid-others-100: #ffc54d;
  --color-goldenrod-100: #e8b644;
  --color-dimgray: #474761;
  --color-gray-100: #878b91;
  --color-gray-200: #191d23;
  --color-gray-300: #0b1023;
  --color-gray-500: rgba(255, 255, 255, 0.1);
  --color-gray-400: rgba(255, 255, 255, 0.8);
  --color-solid-dark-05: rgba(25, 29, 35, 0.05);
  --color-gray-600: rgba(0, 0, 0, 0);
  --color-solid-medium-2-5: rgba(124, 130, 155, 0.05);
  --color-solid-medium-2-10: rgba(124, 130, 155, 0.1);
  --color-lavender: #dae1fb;
  --color-solid-secondary-100: #df142d;
  --color-solid-secondary-06: rgba(223, 20, 45, 0.06);
  --color-royalblue-100: #177cfd;
  --color-solid-primary-100: #0a6ada;
  --color-whitesmoke-100: #f7f9fb;
  --color-whitesmoke-200: #f5f6f6;
  --color-whitesmoke-300: #eef0f4;
  --color-gainsboro-100: #e6e6e9;
  --color-gainsboro-200: #e5e5e8;
  --color-gainsboro-300: #dadde3;
  --color-midnightblue: #22228b;
  --color-slategray-100: #818293;
  --color-slategray-200: #6c738f;
  --color-slategray-300: #596181;
  --color-slategray-400: #475073;
  --color-steelblue-100: #6c86b1;
  --color-steelblue-200: #3c6097;
  --color-steelblue-300: rgba(108, 134, 177, 0.2);
  --color-silver: #b6b7b9;
  --color-solid-primary-2-100: #163668;
  --color-darkslateblue-100: #022357;
  --color-aliceblue-100: #eaeff6;
  --color-aliceblue-200: #e2e8f2;
  --color-ghostwhite: #f3f5fa;
  --red-1: #fdf1f2;
  --color-lightsteelblue-100: #b8c1de;
  --color-lightsteelblue-200: #a2a8bf;
  --color-lightgray: #cccfd9;

  /* Gaps */
  --gap-3xs: 10px;
  --gap-13xl: 32px;
  --gap-7xs: 6px;
  --gap-20xl: 39px;
  --gap-5xs: 8px;

  /* Paddings */
  --padding-12xs: 1px;
  --padding-8xs: 5px;
  --padding-5xs: 8px;
  --padding-smi: 13px;

  /* border radiuses */
  --br-161xl: 180px;
  --br-9xs: 4px;
  --br-7xs: 6px;
  --br-5xs: 8px;
  --br-6xs-2: 6.2px;
  --br-30xl: 49px;
  --br-11xs: 2px;
  --br-base: 16px;
  --br-xs: 12px;
  --br-13xl: 32px;
  --br-mini: 15px;
  --br-mid-5: 17.5px;
  --br-sm: 14px;
}
